.Skills {
  position: relative;
  padding: 0 20px 20px;
  .skill {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    border-radius: 6px;
    margin-bottom: 20px;
    .form {
      display: flex;
      .buttons {
        display: flex;
        justify-content: center;
        width: 290px;
        height: 40px;
        margin-bottom: 20px;
      }
    }
  }
}
